// searchSlice.ts   
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { Product } from './productsSlice';
import { customFetch } from '../../service/apiService';

export const fetchSearchedProducts = createAsyncThunk(
  'search/fetchProducts',
  async (searchQuery: string, thunkAPI) => {
    try {
      const queryString = `q=${searchQuery}`;  //       

      const response = await customFetch(`/products?${queryString}&includeUnavailable=false`);

      if (!response.ok) {
        const data = await response.json();
        throw new Error(data.message || '   ');
      }

      return await response.json(); // ,     
    } catch (error) {
      throw new Error('   ');
    }
  }
);

export const fetchSearchedProductsCount = createAsyncThunk(
  'search/fetchTotalCount',
  async (searchQuery: string, thunkAPI) => {
    try {
      const queryString = `q=${searchQuery}`;  //       

      const response = await customFetch(`/products/total?${queryString}&includeUnavailable=false`);
      
      if (!response.ok) {
        const data = await response.json();
        throw new Error(data.message || '     ');
      }
      return await response.json(); // ,     
    } catch (error) {
      throw new Error('     ');
    }
  }
);

export const searchSlice = createSlice({
    name: 'search',
    initialState: {
      products: [] as Product[],
      total: 0,
      status: 'idle',
      error: null as string | null,
      searchQuery: '', //     
      isSearchOpen: false, //   ,   
    },
    reducers: {
        //  ...
        setSearchOpen(state, action) {
          console.log('Setting search open with:', action.payload);
          state.isSearchOpen = action.payload; // payload     true/false
        },
        setSearchQuery(state, action) {
          state.searchQuery = action.payload; // payload   
        },
        clearSearch(state) {
          //         
          state.searchQuery = '';
          state.products = [];
          state.total = 0;
          //      ,   
        },
        resetSearch(state) {
          //         
          state.isSearchOpen = false;
          state.searchQuery = '';
          state.products = [];
          state.total = 0;
          //      ,   
        },
        // ...   ...
    },
    extraReducers: (builder) => {
        builder
        .addCase(fetchSearchedProducts.pending, (state) => {
            state.status = 'loading';
        })
        .addCase(fetchSearchedProducts.fulfilled, (state, action) => {
            state.status = 'succeeded';
            state.products = action.payload; //   ,      
        })
        .addCase(fetchSearchedProducts.rejected, (state, action) => {
            state.status = 'failed';
            state.error = action.error.message || '   ';
        })
        .addCase(fetchSearchedProductsCount.fulfilled, (state, action) => {
            state.total = action.payload; //   ,      
        })
        // ...     ...
  },
});

export const { setSearchOpen, setSearchQuery, resetSearch, clearSearch } = searchSlice.actions;
export default searchSlice.reducer;
